            Stores.deduction.f529_contribution.amt.value = (deduction === null) ? 0 : deduction.f529_contribution.amt
            Stores.deduction.utma_contribution.enabled.value = (deduction === null) ? false : deduction.utma_contribution.enabled
            Stores.deduction.utma_contribution.amt.value = (deduction === null) ? 0 : deduction.utma_contribution.amt
            Stores.deduction.roth_ira.enabled.value = (deduction === null) ? false : deduction.roth_ira.enabled
